*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background: var(--background);
  color: var(--text-color);
  font-family: var(--font-inter);
  font-size: 16px;
  margin: 0 1rem;
  min-height: calc(100vh - 0.1px);
  padding: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.attribution {
  margin-top: 2rem;
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

:root {
  --green: hsl(75, 94%, 57%);
  --white: hsl(0, 0%, 100%);
  --grey700: hsl(0, 0%, 20%);
  --grey800: hsl(0, 0%, 12%);
  --grey900: hsl(0, 0%, 8%);
  --background: var(--grey900);
  --text-color: var(--white);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  max-width: 384px;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}